// src/app/billing/page.tsx
'use client';

import { Download, Eye, Filter, MoreVertical, Send } from 'lucide-react';
import { Card } from '@/components/ui/Card';

export default function BillingPage() {
    const invoices = [
        { id: 'INV-001', client: 'Acme Corp', amount: 2450, date: '2024-01-15', status: 'paid', dueDate: '2024-01-30' },
        { id: 'INV-002', client: 'Globex Inc', amount: 1800, date: '2024-01-14', status: 'pending', dueDate: '2024-01-29' },
        { id: 'INV-003', client: 'Stark Industries', amount: 3200, date: '2024-01-13', status: 'paid', dueDate: '2024-01-28' },
        { id: 'INV-004', client: 'Wayne Enterprises', amount: 1200, date: '2024-01-12', status: 'overdue', dueDate: '2024-01-27' },
        { id: 'INV-005', client: 'Umbrella Corp', amount: 2750, date: '2024-01-11', status: 'pending', dueDate: '2024-01-26' },
    ];

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'paid': return 'bg-green-100 text-green-800';
            case 'pending': return 'bg-blue-100 text-blue-800';
            case 'overdue': return 'bg-red-100 text-red-800';
            default: return 'bg-gray-100 text-gray-800';
        }
    };

    return (
        <div className="space-y-6 animate-fade-in">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Billing</h1>
                    <p className="text-gray-600 mt-2">Manage client invoices and billing cycles</p>
                </div>
                <div className="flex space-x-3">
                    <button className="px-4 py-2.5 border border-gray-300 text-gray-700 font-medium rounded-xl hover:bg-gray-50 transition-colors flex items-center">
                        <Filter className="h-4 w-4 mr-2" />
                        Filter
                    </button>
                    <button className="px-4 py-2.5 bg-primary text-white font-medium rounded-xl hover:bg-primary/90 transition-colors shadow-lg shadow-primary/25">
                        Create Invoice
                    </button>
                </div>
            </div>

            {/* Billing Summary */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <Card className="p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-600">Total Revenue</p>
                            <p className="text-2xl font-bold mt-1 text-gray-900">$24,580</p>
                        </div>
                        <div className="p-3 bg-green-100 rounded-xl">
                            <div className="text-2xl font-bold text-green-600">↑</div>
                        </div>
                    </div>
                    <p className="text-sm text-gray-500 mt-3">+12.5% from last month</p>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-600">Pending Invoices</p>
                            <p className="text-2xl font-bold mt-1 text-gray-900">$4,250</p>
                        </div>
                        <div className="p-3 bg-blue-100 rounded-xl">
                            <div className="text-2xl font-bold text-blue-600">!</div>
                        </div>
                    </div>
                    <p className="text-sm text-gray-500 mt-3">7 invoices pending</p>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-600">Overdue</p>
                            <p className="text-2xl font-bold mt-1 text-gray-900">$1,200</p>
                        </div>
                        <div className="p-3 bg-red-100 rounded-xl">
                            <div className="text-2xl font-bold text-red-600">↓</div>
                        </div>
                    </div>
                    <p className="text-sm text-gray-500 mt-3">1 invoice overdue</p>
                </Card>
            </div>

            {/* Invoices Table */}
            <Card>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead>
                            <tr className="border-b border-gray-200">
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Invoice ID</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Client</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Amount</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Date</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Due Date</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Status</th>
                                <th className="text-left py-3 px-4 font-semibold text-gray-900">Actions</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-100">
                            {invoices.map((invoice) => (
                                <tr key={invoice.id} className="hover:bg-gray-50/50 transition-colors">
                                    <td className="py-4 px-4 font-medium text-gray-900">{invoice.id}</td>
                                    <td className="py-4 px-4">
                                        <span className="text-gray-900">{invoice.client}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className="font-bold text-gray-900">${invoice.amount.toLocaleString()}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className="text-gray-600">{invoice.date}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className="text-gray-600">{invoice.dueDate}</span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(invoice.status)}`}>
                                            {invoice.status.charAt(0).toUpperCase() + invoice.status.slice(1)}
                                        </span>
                                    </td>
                                    <td className="py-4 px-4">
                                        <div className="flex space-x-2">
                                            <button className="p-2 text-gray-600 hover:text-primary hover:bg-primary/5 rounded-lg transition-colors" title="View">
                                                <Eye className="h-4 w-4" />
                                            </button>
                                            <button className="p-2 text-gray-600 hover:text-blue-600 hover:bg-blue-50 rounded-lg transition-colors" title="Send">
                                                <Send className="h-4 w-4" />
                                            </button>
                                            <button className="p-2 text-gray-600 hover:text-green-600 hover:bg-green-50 rounded-lg transition-colors" title="Download">
                                                <Download className="h-4 w-4" />
                                            </button>
                                            <button className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors">
                                                <MoreVertical className="h-4 w-4" />
                                            </button>
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {/* Table Footer */}
                <div className="flex items-center justify-between border-t border-gray-200 pt-6 px-4">
                    <div className="text-sm text-gray-500">
                        Showing 5 of 42 invoices
                    </div>
                    <div className="flex space-x-2">
                        <button className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm hover:bg-gray-50 transition-colors">
                            Previous
                        </button>
                        <button className="px-3 py-1.5 bg-primary text-white rounded-lg text-sm hover:bg-primary/90 transition-colors">
                            1
                        </button>
                        <button className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm hover:bg-gray-50 transition-colors">
                            2
                        </button>
                        <button className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm hover:bg-gray-50 transition-colors">
                            3
                        </button>
                        <button className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm hover:bg-gray-50 transition-colors">
                            Next
                        </button>
                    </div>
                </div>
            </Card>
        </div>
    );
}